  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 374px 374px;
  column-gap: 17px;
  row-gap: 20px;
  list-style-type: none;
}

.catalog__item {
  display: flex;
  flex-direction: column;
}
.catalog__item h3 {
  margin: 0 auto;
  max-width: 270px;
  font-size: 24px;
  font-family: "DisketMono", "Arial", sans-serif;
  font-weight: 700;
  line-height: 89.023%;
  color: #363844;
  text-align: center;
}
.catalog__item span {
  margin: 0 auto;
  font-size: 20px;
  font-family: "Open-Sans", "Arial", sans-serif;
  font-weight: 300;
  line-height: 130%;
  color: #363844;
}

.catalog__link {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.catalog__link::after {
  visibility: hidden;
  position: absolute;
  content: "";
  width: 65px;
  height: 65px;
  background-color: #ff552f;
  left: -3px;
  top: -3px;
  z-index: -1;
  transition: visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
}
.catalog__link::before {
  visibility: hidden;
  position: absolute;
  content: "";
  width: 65px;
  height: 65px;
  background-color: #ff552f;
  right: -3px;
  top: -3px;
  z-index: -1;
  transition: visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
}
.catalog__link:hover::before, .catalog__link:hover::after, .catalog__link:active::before, .catalog__link:active::after {
  visibility: visible;
}
.catalog__link:focus {
  outline: none;
}
.catalog__link:focus::before, .catalog__link:focus::after {
  visibility: visible;
}

.catalog__image-wrapper {
  margin-bottom: 35px;
  display: flex;
  flex-direction: column;
  flex-grow: 0.5;
  align-items: center;
  background-color: #efefef;
  border-start-start-radius: 5px;
  border-start-end-radius: 5px;
}
.catalog__image-wrapper--add-image {
  flex-grow: 0.77;
}

.catalog__image-text {
  margin: 5px 0 0 0;
  max-width: 126px;
  font-size: 16px;
  font-family: "Open-Sans", "Arial", sans-serif;
  font-weight: 600;
  line-height: 130%;
  color: #a5a5a5;
  text-align: center;
}
.catalog__image-text--add-image {
  display: none;
}

.catalog__image {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  width: 357px;
  height: 152px;
  margin-top: auto;
  /*&::before {
    content: "";
    display: block;
    padding-bottom: calc(68 / 120 * 100%);
  }*/
}
.catalog__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.catalog__description {
  min-height: 85px;
  padding-top: 18px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.catalog__decor {
  position: relative;
  overflow: hidden;
  width: 127px;
  height: 99px;
  margin-top: 62px;
  /*&::before {
    content: "";
    display: block;
    padding-bottom: calc(80 / 120 * 100%);
  }*/
}
.catalog__decor img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.catalog__decor--add-image {
  display: none;
}